import logo from "./logo.svg";
import "./App.css";
import StickyHeadTable from "./table.js";
import BasicLineChart from "./plot.js";

import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";

import * as React from "react";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import Input from "@mui/material/Input";
import FilledInput from "@mui/material/FilledInput";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import InputAdornment from "@mui/material/InputAdornment";
import FormHelperText from "@mui/material/FormHelperText";
import FormControl from "@mui/material/FormControl";
import TextField from "@mui/material/TextField";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import SendIcon from '@mui/icons-material/Send';

import Button from "@mui/material/Button";

const darkTheme = createTheme({
    palette: {
        mode: "dark",
    },
});

function App() {
    const [showPassword, setShowPassword] = React.useState(false);

    const handleClickShowPassword = () => setShowPassword((show) => !show);

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    return (
        <ThemeProvider theme={darkTheme}>
            <CssBaseline />
            <div className="App">
                <div className="header">
                    <FormControl
                        sx={{ m: 0.5, width: "12ch", mb:1  }}
                        variant="outlined"
                        size='small'
                    >
                        <InputLabel htmlFor="outlined-adornment-username" sx={{fontSize:"14px"}}>
                            Name
                        </InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-username"
                            label="Username"
                        />
                    </FormControl>

                    <FormControl
                        sx={{ m: 0.5, width: "12ch", mb:1  }}
                        variant="outlined"
                        size='small'
                    >
                        <InputLabel htmlFor="outlined-adornment-password" sx={{fontSize:"14px"}}>
                            Key
                        </InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password"
                            type={showPassword ? "text" : "password"}
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {showPassword ? (
                                            <VisibilityOff />
                                        ) : (
                                            <Visibility />
                                        )}
                                    </IconButton>
                                </InputAdornment>
                            }
                            label="Password"
                        />
                    </FormControl>

                    <FormControl
                        sx={{ m: 0.5, width: "9ch", mb:1 }}
                        variant="outlined"
                        size='small'
                    >
                        <OutlinedInput
                            id="outlined-adornment-weight"
                            endAdornment={
                                <InputAdornment position="end" sx={{fontSize:"14px"}}>
                                    kg
                                </InputAdornment>
                            }
                            aria-describedby="outlined-weight-helper-text"
                            inputProps={{
                                "aria-label": "weight",
                            }}
                        />
                    </FormControl>

                    <Button variant="contained" size="small" sx={{ m: 0.5, minWidth: "5ch", width:"5ch", mb:1  }}>
                    <SendIcon sx={{transform: "scale(0.8)"}} />
                    </Button>
                </div>

                <div className="body">
                    <div className="weight-plot">
                        <BasicLineChart />
                    </div>

                    <div className="weight-table">
                        <StickyHeadTable />
                    </div>
                </div>
            </div>
        </ThemeProvider>
    );
}

export default App;
